@Composable
fun socialBar() {
    Column(
        verticalArrangement = Arrangement.Center,
        horizontalAlignment = Alignment.CenterHorizontally,
        modifier = Modifier
            .margin(right = 25.px)
            .padding(topBottom = 15.px)
            .borderRadius(r = 20.px)
            .backgroundColor(Colors.White)
    ) {
        socialLink()
    }
}

@Composable
private fun socialLink() {
    Link(path = WEBSITE, openExternalLinksStrategy = OpenLinkStrategy.IN_NEW_TAB) {
        FaFacebook(
            modifier = SocialLinkStyle.toModifier().margin(bottom = 40.px),
            size = IconSize.LG,
        )
    }
    Link(path = WEBSITE, openExternalLinksStrategy = OpenLinkStrategy.IN_NEW_TAB) {
        FaTwitter(
            modifier = SocialLinkStyle.toModifier().margin(bottom = 40.px),
            size = IconSize.LG,
        )
    }
    Link(path = WEBSITE, openExternalLinksStrategy = OpenLinkStrategy.IN_NEW_TAB) {
        FaInstagram(
            modifier = SocialLinkStyle.toModifier().margin(bottom = 40.px),
            size = IconSize.LG,
        )
    }
    Link(path = WEBSITE, openExternalLinksStrategy = OpenLinkStrategy.IN_NEW_TAB) {
        FaLinkedin(
            modifier = SocialLinkStyle.toModifier().margin(bottom = 40.px),
            size = IconSize.LG,
        )
    }
}