package com.honger.ngel.sections

import androidx.compose.runtime.Composable
import com.honger.ngel.components.header
import com.honger.ngel.components.socialBar
import com.honger.ngel.model.Section
import com.honger.ngel.model.Theme
import com.honger.ngel.style.MainButtonStyle
import com.honger.ngel.style.MainImageStyle
import com.honger.ngel.utils.Constants.FONT_FAMILY
import com.honger.ngel.utils.Constants.INTRO_SHORTEST
import com.honger.ngel.utils.Constants.SECTION_WIDTH
import com.honger.ngel.utils.Res
import com.honger.ngel.utils.Res.Image.IC_MAIN_IMAGE
import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.graphics.Colors
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.layout.SimpleGrid
import com.varabyte.kobweb.silk.components.layout.numColumns
import com.varabyte.kobweb.silk.components.navigation.Link
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.P
import org.jetbrains.compose.web.dom.Text

@Composable
fun mainSection() {

    Box(
        modifier = Modifier
            .id(Section.Home.id)
            .maxWidth(SECTION_WIDTH.px),
        contentAlignment = Alignment.TopCenter
    ) {
        mainBackground()
        mainContent()
    }
}

@Composable
fun mainBackground() {
    Image(
        modifier = Modifier.fillMaxSize().objectFit(ObjectFit.Cover),
        src = Res.Image.IC_BACKGROUND,
        description = "Background Image"
    )
}

@Composable
fun mainContent() {
    val breakpoint = rememberBreakpoint()
    Column(
        Modifier.fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally
    ) {
        header()
        Column(
            modifier = Modifier.fillMaxWidth(),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Bottom
        ) {
            SimpleGrid(
                modifier = Modifier.fillMaxWidth(
                    if (breakpoint >= Breakpoint.MD) 80.percent else 90.percent
                ),
                numColumns = numColumns(base = 1, md = 2)
            ) {
                mainText(breakpoint)
                mainImage()
            }
        }
    }
}

@Composable
fun mainText(breakpoint: Breakpoint) {

    Row(
        horizontalArrangement = Arrangement.Center,
        verticalAlignment = Alignment.CenterVertically
    ) {
        if (breakpoint > Breakpoint.MD) {
            socialBar()
        }
        Column {
            P(
                attrs = Modifier
                    .margin(topBottom = 0.px)
                    .fontFamily(FONT_FAMILY)
                    .fontSize(if (breakpoint >= Breakpoint.MD) 45.px else 20.px)
                    .color(Theme.Primary.rgb)
                    .fontWeight(FontWeight.Bolder)
                    .toAttrs()
            ) {
                Text("Hello, I'm")
            }
            P(
                attrs = Modifier
                    .margin(top = 20.px, bottom = 0.px)
                    .fontFamily(FONT_FAMILY)
                    .fontSize(if (breakpoint >= Breakpoint.MD) 68.px else 40.px)
                    .color(Theme.Secondary.rgb)
                    .fontWeight(FontWeight.Bolder)
                    .toAttrs()
            ) {
                Text("Jonathan Smith")
            }
            P(
                attrs = Modifier
                    .margin(top = 10.px, bottom = 5.px)
                    .fontFamily(FONT_FAMILY)
                    .fontSize(20.px)
                    .color(Theme.Secondary.rgb)
                    .fontWeight(FontWeight.Bolder)
                    .toAttrs()
            ) {
                Text("Mobile & Web Developer/Designer")
            }
            P(
                attrs = Modifier
                    .margin(bottom = 25.px)
                    .fontFamily(FONT_FAMILY)
                    .maxWidth(400.px)
                    .fontSize(15.px)
                    .fontStyle(FontStyle.Initial)
                    .color(Theme.Secondary.rgb)
                    .fontWeight(FontWeight.Normal)
                    .toAttrs()
            ) {
                Text(INTRO_SHORTEST)
            }
            Button(
                attrs = MainButtonStyle.toModifier()
                    .backgroundColor(Theme.Primary.rgb)
                    .color(Colors.White)
                    .border(width = 0.px)
                    .borderRadius(r = 5.px)
                    .height(40.px)
                    .cursor(Cursor.Pointer)
                    .toAttrs()
            ) {
                Link(
                    modifier = Modifier
                        .color(Colors.White)
                        .textDecorationLine(TextDecorationLine.None),
                    text = "Hire me",
                    path = Section.Contact.path
                )
            }
        }
    }
}


@Composable
fun mainImage() {
    Column(
        modifier = Modifier.fillMaxSize(80.percent).fillMaxHeight(),
        verticalArrangement = Arrangement.Center,
    ) {
        Image(
            modifier = MainImageStyle.toModifier().fillMaxSize(),
            src = IC_MAIN_IMAGE,
            description = "Main Image"
        )
    }
}

